:root {
  --body-color: #fff;
  --mobail-modal: #9bb537;
  --theme-light: #f8f8f8;
  --theme-dark: #1e1e1e;
  --accent-color: #9bb537;
  --dark-text-title: #000;
  --white-text-title: #fff;
  --dark-text: #050505;
  --input-color-theme-dark: rgba(255, 255, 255, .5);
  --input-color-theme-light: rgba(5, 5, 5, .3);
  --stars-color: #eea10c;
  --recipes-text: rgba(5, 5, 5, .8);
  --bordes-radius-slick: rgba(155, 181, 55, .3);
  --modal-rating-theme-light: #47906a;
  --background-color-recipes: rgba(46, 47, 66, .4);
  --modal-measure-recipes: rgba(5, 5, 5, .5);
  --modal-name-ingradient: rgba(5, 5, 5, .1);
  --modal-hashtag-item: rgba(5, 5, 5, .2);
  --modal-recipe-instruction: rgba(5, 5, 5, .8);
  --gal-form-col: rgba(5, 5, 5, .5);
  --gal-form-border-col: rgba(5, 5, 5, .2);
  --btn-all-categories: rgba(5, 5, 5, .5);
  --text-category: rgba(5, 5, 5, .18);
  --hero-bg-slide: #050505;
}

@media screen and (max-width: 767px) {
  body.off {
    overflow: hidden;
  }

  .header {
    padding-top: 20px;
    display: flex;
  }

  .header-nav {
    display: none;
  }

  .header-bth-burger {
    background-color: rgba(0, 0, 0, 0);
    border: none;
  }

  .header-burger-icon {
    stroke: var(--dark-text);
    transition: stroke .25s cubic-bezier(.4, 0, .2, 1);
  }

  .header-burger-icon:focus, .header-burger-icon:hover {
    stroke: var(--accent-color);
  }

  .switch {
    display: none;
  }

  .header-burger-modal-window {
    z-index: 10;
    width: 100%;
    height: 100%;
    background-color: var(--mobail-modal);
    flex-direction: column;
    justify-content: space-between;
    display: flex;
    position: absolute;
    top: 0;
    right: 0;
  }

  .header-bth-modal-close {
    background-color: rgba(0, 0, 0, 0);
    border: none;
    position: absolute;
    top: 20px;
    right: 20px;
  }

  .header-icon-close {
    stroke: var(--theme-light);
  }

  .modal-header-nav {
    text-align: center;
    flex-direction: column;
    justify-content: center;
    gap: 16px;
    padding-top: 200px;
    font-family: Inter;
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    display: flex;
  }

  .modal-header-nav-Favorites, .modal-header-nav-home {
    color: var(--theme-light);
  }

  .mobail-switch {
    width: 46px;
    height: 20px;
    margin: 20px;
    position: relative;
  }

  .mobail-switch input {
    display: none;
  }

  .mobail-header-check-circle {
    cursor: pointer;
    width: 46px;
    background-color: #cecdcd;
    transition: all .4s;
    display: inline-block;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }

  .mobail-header-check-circle:before {
    content: "";
    height: 19px;
    width: 19px;
    background-color: var(--theme-light);
    transition: all .4s;
    position: absolute;
    bottom: .5px;
    left: .5px;
  }

  input:checked + .mobail-header-check-circle {
    background-color: var(--accent-color);
  }

  input:focus + .mobail-header-check-circle {
    box-shadow: 0 0 1px var(--accent-color);
  }

  input:checked + .mobail-header-check-circle:before {
    transform: translateX(26px);
  }

  .mobail-header-check-circle.mobail-round {
    border-radius: 34px;
  }

  .mobail-header-check-circle.mobail-round:before {
    border-radius: 50%;
  }
}

